import { useCallback, useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { PaginationRequest, TableColumn } from "../../../../../models/baseModel";
import { VariableService } from "../../../../../services/taskService";
import { MyBox } from "../../../../core/MyBox";
import { MyButton } from "../../../../core/MyButton";
import { OrderByModel, PageInfoModel } from "../../../../core/TableModel";
import TableComponent from "../../../../core/TableNewComponent";
import { UIFormatDateTimeVNComponent } from "../../../../core/UI_FormatComponent";
import ModifyComponent from "./ModifyComponent";

export default function IndexComponent() {
    const projectKey = useParams().key;
    const column: any = [
        new TableColumn({ title: "STT", key: "stt" }),
        new TableColumn({
            title: "Tên", key: "name", render: (data: string, row: any) => {
                return <span onClick={() => {
                    handleClickEdit(row.key)
                }}>{data}</span>
            }
        }),
        new TableColumn({ title: "Giá trị khởi tạo", key: "initalValue" }),
        new TableColumn({ title: "Giá trị hiện tại", key: "currentValue" }),
        new TableColumn({
            title: "Ngày tạo", key: "createdAt", render: (data: string) => {
                return <UIFormatDateTimeVNComponent value={data} />
            }
        }),
        new TableColumn({
            title: "Ngày cập nhật cuối", key: "updatedAt", render: (data: string) => {
                return <UIFormatDateTimeVNComponent value={data} />
            }
        })
    ];
    const [isLoading, setIsLoading] = useState(false);
    const [pageInfo, setPageInfo] = useState<PageInfoModel>(new PageInfoModel());
    const [orderBy, setOrderBy] = useState<OrderByModel>(new OrderByModel());
    const [data, setData] = useState([]);
    const [visible, setVisible] = useState(false);
    const [visibleEdit, setVisibleEdit] = useState(false);
    const [keyEdit, setKeyEdit] = useState<string | undefined>();

    const loadData = useCallback(() => {
        var paginationRequest = new PaginationRequest(pageInfo.pageNumber, pageInfo.pageSize, orderBy);
        setIsLoading(true);
        VariableService.GetList(projectKey, paginationRequest, (data: any) => {
            var row = data.data.data.pagedData.map((data: any, index: number) => {
                return {
                    key: data.key,
                    stt: index + 1,
                    name: data.name,
                    initalValue: data.initalValue,
                    currentValue: data.currentValue,
                    createdAt: data.createdAt,
                    updatedAt: data.updatedAt
                }
            })
            setData(row);
        }, undefined, () => {
            setIsLoading(false);
        })

    }, [projectKey, pageInfo.pageNumber, pageInfo.pageSize, orderBy]);

    useEffect(() => {
        loadData();
    }, [loadData])

    function handleClickAdd() {
        setVisible(true);
    }

    function handleClickEdit(key: string | undefined) {
        setKeyEdit(key);
        setVisibleEdit(true);
    }

    return (
        <MyBox
            title="Quản lý biến"
            isLoading={isLoading}>
            <MyButton type="primary" value="Thêm mới" onClick={handleClickAdd} />
            <TableComponent
                pageInfo={pageInfo}
                setPageInfo={setPageInfo}
                orderBy={orderBy}
                setOrderBy={setOrderBy}
                column={column}
                dataSource={data}
                loading={isLoading}
                loadData={loadData} />

            <ModifyComponent
                isLoading={isLoading}
                setIsLoading={setIsLoading}
                visible={visible}
                setVisible={setVisible}
                projectKey={projectKey}
                loadData={loadData} />

            <ModifyComponent
                isLoading={isLoading}
                setIsLoading={setIsLoading}
                visible={visibleEdit}
                setVisible={setVisibleEdit}
                keyData={keyEdit}
                setKeyData={setKeyEdit}
                projectKey={projectKey}
                loadData={loadData} />
        </MyBox>
    );
}