import { Drawer, Form } from "antd";
import { useCallback, useEffect, useState } from "react";
import { Fn } from "../../../../../helper/function";
import { VariableService } from "../../../../../services/taskService";
import { MyFormItem } from "../../../../core/Input";
import { MyButton } from "../../../../core/MyButton";

export default function ModifyComponent(props: any) {
    const [name, setName] = useState('');
    const [initalValue, setInitalValue] = useState('');
    const [currentValue, setCurrentValue] = useState('');

    const loadData = useCallback(() => {
        if (props.keyData !== undefined) {
            VariableService.GetDetail(props.projectKey, props.keyData, (data: any) => {
                let row = data.data.data;
                setName(row.name);
                setInitalValue(row.initalValue);
                setCurrentValue(row.currentValue);
            })
        }
    }, [props.keyData, props.projectKey]);

    useEffect(() => {
        loadData();
    }, [loadData])

    function handleOnChangeInitalValue(value: string) {
        setCurrentValue(value);
    }

    function handleSubmitForm() {
        let body = {
            name: name,
            initalValue: initalValue,
            currentValue: currentValue
        };

        props.setIsLoading(true);
        if (props.keyData !== undefined) {
            Edit(body);
        } else {
            Add(body);
        }
    }

    function Add(body: any) {
        VariableService.Add(props.projectKey, body, (data: any) => {
            Fn.PrintMsgSuccess(data);
            HandleOnClose();
            props.setVisible(false);
            props.loadData();
        }, () => {
            props.setIsLoading(false);
        })
    }

    function Edit(body: any) {
        VariableService.GetDetail(props.projectKey, props.keyData, (data: any) => {
            body = Fn.GetTimestamp(data, body);
            VariableService.Edit(props.projectKey, props.keyData, body, (data: any) => {
                Fn.PrintMsgSuccess(data);
                HandleOnClose();
                props.setVisible(false);
                props.loadData();
            }, () => {
                props.setIsLoading(false);
            })
        }, () => {
            props.setIsLoading(false);
        })
    }

    function HandleOnClose() {
        setName('');
        setCurrentValue('');
        setInitalValue('');
        props.setVisible(false);
        if (props.setKeyData !== undefined) {
            props.setKeyData(undefined);
        }
    }

    function HandleDelete() {
        props.setIsLoading(true);
        VariableService.GetDetail(props.projectKey, props.keyData, (data: any) => {
            VariableService.Delete(props.projectKey, props.keyData, Fn.GetTimestamp(data), (data: any) => {
                Fn.PrintMsgSuccess(data);
                HandleOnClose();
                props.setVisible(false);
                props.loadData();
            }, () => {
                props.setIsLoading(false);
            })
        }, () => {
            props.setIsLoading(false);
        })
    }

    return (
        <Drawer
            open={props.visible}
            onClose={HandleOnClose}
            title={props.keyData !== undefined ? "Chỉnh sửa" : "Thêm mới"}>
            <Form
                onSubmitCapture={handleSubmitForm}
                layout="vertical">
                <MyFormItem.Input
                    label="Tên"
                    value={name}
                    setValue={setName} />

                <MyFormItem.Input
                    label="Giá trị khởi tạo"
                    readOnly={props.keyData !== undefined}
                    onChange={handleOnChangeInitalValue}
                    value={initalValue}
                    setValue={setInitalValue} />

                <MyFormItem.Input
                    label="Giá trị hiện tại"
                    readOnly={props.keyData === undefined}
                    value={currentValue}
                    setValue={setCurrentValue} />

                <MyButton
                    htmlType="submit"
                    type="success"
                    value={props.keyData === undefined ? "Thêm mới" : "Chỉnh sửa"} />

                <MyButton
                    type="danger"
                    onClick={HandleDelete}
                    disabled={props.keyData === undefined}
                    value="Xóa" />
            </Form>
        </Drawer>
    );
}